* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

header {
  justify-content: center;
  margin-top: 8px;
  display: flex;
}

#searchForm {
  border: 1px solid gray;
  border-radius: 4px;
  flex-direction: row;
  width: 25%;
  height: 2rem;
  display: flex;
  box-shadow: 0 5px 15px #00000059;
}

#searchForm > input {
  border: none;
  flex-grow: 2;
  padding: .375rem 0 .375rem .5rem;
}

#searchForm > input:focus {
  outline: none;
}

#searchForm > button {
  background: none;
  border: none;
  padding-right: .8rem;
}

#searchForm > button:hover {
  cursor: pointer;
}

#searchForm > button > i {
  color: gray;
}

#productContainer {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  margin: 2rem;
  display: grid;
}

.product-card {
  box-shadow: 0 48px 100px #110c2e26;
}

.img-container {
  object-fit: cover;
  height: 300px;
}

.img-container > img {
  width: 100%;
  height: 100%;
}

.product-card-content {
  padding: 1rem;
}

.product-heading {
  justify-content: space-between;
  margin-top: 1rem;
  display: flex;
}

.product-description {
  margin: 1rem 0;
  font-style: italic;
}

.product-btn {
  margin-top: .5rem;
  padding: .2rem .4rem;
}

.warning {
  color: red;
  font-style: italic;
}

